.songRow {
  margin-left: 20px;
  padding: 20px;
  display: flex;
  align-items: center;
  color: #fff;
}

.songRow:hover {
  cursor: pointer;
  background-color: #000;
  opacity: .8;
}

.songRow__info {
  margin-left: 20px;
}

.songRow__info > h1 {
  font-size: 16px;
}

.songRow__album {
  height: 40px;
  width: 40px;
}